body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.drum-pad {
  display: block;
  width: 30px;
  height: 30px;
  background-color: black;
  color: white;
  text-decoration: none;
}


#drum-machine {
  font-family: sans-serif;
  width: 250px;
  height: 200px;
  margin: auto;
  display: grid;
  grid-template-areas:
      "display display display"
      "heater-1 heater-2 heater-3"
      "side-stick snare open-hi-hat"
      "kick-n-hat kick punchy-kick";
  margin-top: 20vh;
}

.drum-pad {
  font-size: 3em;
  text-align: center;
  width: 95%;
  height: 95%;
  border-radius: 10%;
}

#display {
  grid-area: display;
  font-size: 2em;
  height: 50px;
  width: 97.5%;
  background-color: white;
  color: black;
  text-align: center;
  font-weight: bold;
}

#heater-1 {
  grid-area: heater-1;  
}

#heater-2 {
  grid-area: heater-2;  
}

#heater-3 {
  grid-area: heater-3;  
}

#side-stick {
  grid-area: side-stick;
} 

#snare {
  grid-area: snare;
}

#open-hi-hat {
  grid-area: open-hi-hat;
}

#kick-n-hat {
  grid-area: kick-n-hat;
}

#kick {
  grid-area: kick;
}

#punchy-kick {
  grid-area: punchy-kick;
}

